form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    /* align-items: center; */
    background-color: #7e0080c4;
    border-radius: 5px;
    color: white;
    padding: 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

form input {
    border-radius: 4px;
    border-color: 00ff5aab;
    font-size: x-large;
    padding: 5px;
}

form label {
    font-size: x-large;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

form h2 {
    margin: auto;
    padding: 5px;
    text-align: center;
}

.title-input {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.title-input input {
    margin-left: 1em;
    width: 100%
}


.time-input {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    margin: 1rem;
}

.time-input input {
    margin-left: 1em;
    width: 3rem;
}

form input[type="submit"] {
    background-color: #00ff5aab;
    color: white;
    font-size: large;
    border-radius: 5px;
    margin: 2rem;
    padding: 5px;
    width: 50%;
    margin: auto;
}

form input[type="submit"]:hover {
    background-color: #00ff5afa;
}