.card {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 1em;
    background-color: aqua;
    color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.5s;
    border-radius: 15px;
    padding: 0.5rem;
}

.card h1 {
    font-size: 4em;
    flex-grow: 4;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
    margin: 0;
}

.card .timer {
    font-size: 18px;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.current {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    border-style: solid;
    border-color: red;
}